import '@/database';
import consign from 'consign';
import express from 'express';
import morgan from 'morgan';
import cors from 'cors';

const App = express();

App.use(cors({ origin: true }));
App.use(express.json());
App.use(express.urlencoded({ extended: false }));
App.use(morgan('dev'));

consign({
  locale: 'pt-br',
})
  .include('src/router')
  .into(App);

export default App;
